@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
}

.page {
  text-align: center;
}

.header-container {
  width: 100%;
  height: 20%;
  background-color: #009999;
  margin-bottom: 20px;
}

.header {
  padding: 20px;
  color: white;
  font-size: 34px;
}

.box {
  padding: 5px;
  margin: auto;
  width: 50%;
  height: 25%;
  border: 3px solid #494949;
}

.box-header {
  font-size: 24px;
  margin-bottom: 10px;
}

.upload {
  font-family: 'Poppins', sans-serif;
  cursor: pointer;
  outline: none;
}

.loader {
  border: 16px solid #f3f3f3;
  border-top: 16px solid #009999;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
  margin: auto;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.file-error {
  color: red;
}

.dont-show-error {
  visibility: hidden;
}

.submit-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  font-family: 'Poppins', sans-serif;
}

.submit-button:hover {
  color: #ffffff;
  background: #f6b93b;
  border-color: #f6b93b;
  transition: all 0.4s ease 0s;
  cursor: pointer;
}

.select {
  margin-bottom: 15px;
}
